
.friends{
    margin: 60px auto 0;
    width: 400px;
    list-style: none;
}
.item{
    display: flex;
    align-items: center;
    width: 300px;
    margin: 0 auto;
    padding: 20px 50px;
    border-radius: 5px;
    margin-bottom: 10px;
    background-color: whitesmoke;
    box-shadow: rgb(0 0 0 / 20%) 0px 3px 1px -2px,
    rgb(0 0 0 / 14%) 0px 2px 2px 0px, rgb(0 0 0 / 12%) 0px 1px 5px 0px;


}